<template>
  <main class="content">
    <div class="content-wrapper container">
      <div class="image-wrapper">
        <button
          class="image__btn-refresh"
          type="button"
          :disabled="!$store.state.pickedColors.size"
          @click="refreshState"
        >Сбросить всё</button>
        <Clothes class-name="constructor-img" icon-name="hoodie"/>
        <p class="price">Цена: 5800 руб.</p>
      </div>
      <ImageOptions />
    </div>
  </main>
</template>

<script>
import ImageOptions from '@/components/ImageOptions'
import Clothes from '@/components/Clothes'

export default {
  name: 'Main',
  components: {
    ImageOptions,
    Clothes
  },
  methods: {
    refreshState() {
      this.$store.commit('refreshState')
    }
  }
}
</script>

<style scoped lang="scss">
  .content {
    padding: 50px 0 100px;
  }

  .content-wrapper {
    display: flex;
    align-items: flex-start;
    justify-content: center;
  }

  .image-wrapper {
    position: relative;
    flex-grow: 1;
    text-align: center;
  }

  .image__btn-refresh {
    position: absolute;
    top: 5px;
    right: 50px;
    width: 50px;
    height: 50px;
    font-size: 0;
    line-height: 0;
    background-color: transparent;
    background-image: url('../assets/icons/refresh.svg');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    transition: opacity 0.5s ease-in;

    &:hover {
      animation: rotate 1s cubic-bezier(.9,-0.4,.05,1.46) infinite;
    }

    &:disabled {
      cursor: default;
      opacity: 0.3;
    }

    &:disabled:hover {
      animation: none;
    }
  }

  @keyframes rotate {
    0% {
      transform: rotate(0);
    }
    100% {
      transform: rotate(180deg);
    }
  }

  .price {
    padding: 50px;
    font-size: 40px;
    font-weight: 800;
    text-align: center;
  }
</style>
