<template>
  <ul class="options">
    <li class="options__item">
      <label class="options__select-label" for="hoodie-type">Тип худи:</label>
      <select class="options__select" name="hoodie-type" id="hoodie-type">
        <option value="type-default" selected>Базовое</option>
        <option value="type-short">Укороченное</option>
        <option value="type-long">Удлинённое</option>
      </select>
    </li>
    <li class="options__item">
      <label class="options__select-label" for="bottom-type">Обработка низа:</label>
      <select class="options__select" name="bottom-type" id="bottom-type">
        <option value="type-default" selected>Без резинки</option>
        <option value="type-short">Кашкорсе</option>
        <option value="type-long">Необработанный срез</option>
      </select>
    </li>
    <li class="options__item">
      <label class="options__select-label" for="hood-type">Тип капюшона:</label>
      <select class="options__select" name="hood-type" id="hood-type">
        <option value="type-default" selected>В тон основной ткани</option>
        <option value="type-short">Цветной</option>
        <option value="type-long">Отсутствует</option>
      </select>
    </li>
    <li class="options__item">
      <label class="options__select-label" for="laces-type">Тип шнурков:</label>
      <select class="options__select" name="laces-type" id="laces-type">
        <option value="type-default" selected>В тон ткани</option>
        <option value="type-short">Цветные</option>
        <option value="type-long">Отсутствуют</option>
      </select>
    </li>
    <li class="options__item">
      <label class="options__select-label">Цвет кашкорсе:</label>
      <p>// В разработке //</p>
    </li>
    <li class="options__item">
      <label class="options__select-label" for="pocket-type">Тип кармана:</label>
      <select class="options__select" name="pocket-type" id="pocket-type">
        <option value="type-default" selected>Накладной</option>
        <option value="type-short">Врезной</option>
        <option value="type-long">Отсутствует</option>
      </select>
    </li>
    <li class="options__item">
      <label class="options__select-label" for="application-type">Нанесение:</label>
      <select class="options__select" name="application-type" id="application-type" v-model="application">
        <option value="type-vishivka" selected>Вышивка</option>
        <option value="type-transfer">Термотрансфер</option>
        <option value="type-strazi">Стразы</option>
      </select>
    </li>
    <li class="options__item" v-if="application === 'type-vishivka'">
      <label class="options__select-label" for="application-type--1">Вышивка:</label>
      <select class="options__select" name="application-type--1" id="application-type--1">
        <option value="type-default" selected>Вышивка на груди</option>
        <option value="type-short">Вышивка на рукаве</option>
        <option value="type-long">Без вышивки</option>
      </select>
    </li>
    <li class="options__item" v-if="application === 'type-transfer'">
      <label class="options__select-label" for="application-type--2">Термотрансфер:</label>
      <select class="options__select" name="application-type--2" id="application-type--2">
        <option value="type-default" selected>Плёнка цветная</option>
        <option value="type-short">Плёнка блескучая</option>
        <option value="type-long">Без плёнки</option>
      </select>
    </li>
    <li class="options__item" v-if="application === 'type-strazi'">
      <label class="options__select-label" for="application-type--3">Стразы:</label>
      <select class="options__select" name="application-type--3" id="application-type--3">
        <option value="type-default" selected>Наша картинка блестяшками</option>
        <option value="type-short">Текст какой-то</option>
        <option value="type-long">Ничего</option>
      </select>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'Options',
  data() {
    return {
      application: 'type-vishivka'
    }
  }
}
</script>

<style scoped lang="scss">
  .options {
    list-style: none;
  }

  .options__item {
    padding: 20px 0 30px;
    border-top: 1px solid #ccc;
  }

  .options__select-label {
    display: block;
    margin-bottom: 5px;
    font-size: 20px;
    font-weight: 600;
  }

  .options__select {
    width: 100%;
    padding: 5px 10px;
    font-size: 20px;
    border: 2px solid #333;
  }
</style>
