<script>
export default {
  name: 'Clothes',
  methods: {
    makeActive(event) {
      const activeLayer = event.target.closest('.layer')
      if (activeLayer) {
        if (this.$refs.svgImage.querySelector('.layer--active')) {
          this.$refs.svgImage.querySelector('.layer--active').classList.remove('layer--active')
        }
        activeLayer.classList.add('layer--active')
        this.$store.commit('setActiveLayer', activeLayer.classList[1])
      }
    }
  },
  mounted() {
    const image = this.$refs.svgImage
    const layers = image.querySelectorAll('.layer')
    this.$store.commit('setImageLayers', [...layers])
  }
}
</script>

