<template>
  <form class="image-options" name="image-options">
    <h2 class="image-options__heading">Худи</h2>
    <div class="image-options__category">
      <h3 class="image-options__category-heading">Цвета</h3>
      <ul class="colors__list">
        <li class="colors__item" v-for="item in colors" :key="item.colorId">
          <input
            class="colors__item-checkbox visuallyhidden"
            type="radio"
            :disabled="checkDisabled(item.colorId)"
            :id="item.colorId"
            :value="item.color"
            name="colors"
            ref="checkbox"
            :checked="activeLayerColor === item.colorId"
            @change="setActiveLayerColor(item.colorId)"
          >
          <label
            class="colors__item-label"
            :for="item.colorId"
            :style="{backgroundColor: `#${item.colorId}`}"
          >
            {{ item.color }}
          </label>
        </li>
      </ul>
      <p class="colors__checked-list">
        Использовано цветов: {{ pickedColorsAmount }} / {{ maxColors }}
      </p>
      <Options/>
    </div>
  </form>
</template>

<script>
import Options from '@/components/Options'

export default {
  name: 'ImageOptions',
  data() {
    return {
      colors: this.$store.state.colors
    }
  },
  components: {
    Options: Options
  },
  methods: {
    setActiveLayerColor(color) {
      this.$store.commit('setActiveLayerColor', color)
      this.markPickedColor()
    },
    markPickedColor() {
      const pickedColors = this.$store.state.pickedColors
      this.$refs.checkbox.forEach(checkbox => {
        if (pickedColors.has(checkbox.id)) {
          checkbox.nextElementSibling.classList.add('colors__item-label--picked')
        } else if (checkbox.nextElementSibling.classList.contains('colors__item-label--picked')) {
          checkbox.nextElementSibling.classList.remove('colors__item-label--picked')
        }
      })
    },
    checkDisabled(colorId) {
      const state = this.$store.state
      if (state.activeLayer) {
        const activeLayerFill = state.clothesLayers[state.activeLayer].fill
        return !state.activeLayer || (!activeLayerFill && this.pickedColorsAmount === 3 && !state.pickedColors.has(colorId))
      } else {
        return !state.activeLayer
      }
    }
  },
  computed: {
    activeLayerColor() {
      const activeLayer = this.$store.state.clothesLayers[this.$store.state.activeLayer]
      if (activeLayer && activeLayer.fill) {
        return activeLayer.fill.slice(1)
      }
      return null
    },
    pickedColorsAmount() {
      return this.$store.state.pickedColors.size
    },
    maxColors() {
      return this.$store.state.maxColors
    }
  }
}
</script>

<style scoped lang="scss">
  .image-options__heading {
    padding-bottom: 20px;
    margin-bottom: 20px;
    font-size: 40px;
    font-weight: 800;
    color: #333;
    border-bottom: 1px solid #ccc;
  }

  .image-options__category-heading {
    margin-bottom: 20px;
    font-size: 30px;
    font-weight: 600;
    color: #333;
  }

  .colors__list {
    display: grid;
    grid-template-columns: repeat(5, 50px);
    grid-template-rows: 50px 50px;
    grid-gap: 15px;
    margin-bottom: 20px;
    list-style: none;
    font-size: 0;
    line-height: 0;
  }

  .colors__item-label {
    position: relative;
    display: block;
    width: 50px;
    height: 50px;
    font-size: 0;
    line-height: 0;
    border-radius: 50%;
    cursor: pointer;
    transition: background-color 0.5s ease-in-out;
  }

  .colors__item-checkbox:checked + .colors__item-label {
    background-image: url('../assets/icons/check-mark.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 50%;
  }

  .colors__item-label--picked::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 4px;
    height: 4px;
    background-color: #333;
  }

  .colors__checked-list {
    padding-bottom: 30px;
  }
</style>
